import React from 'react';
import styles from './factopedia/factopedia.module.css';
import Aside from './factopedia/aside.js';

class Accordion extends React.Component {
  render() {
    const { title, expand, onClick } = this.props;
    
    return (
      <div>
            <div className={expand ? [styles.title, styles.is_expanded].join(" ") : styles.title } onClick={onClick}> {title} </div>
            <div className={expand ? [styles.content, styles.is_expanded].join(" ") : styles.content } onClick={onClick}>
              <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. ec elementum tincidunt.</p>
            </div>
      </div>
    );
    
  }
}
class Application extends React.Component {  
  constructor (props) {
    super(props);
    this.state = {
      block1: true,
      block2: false,
      block3: false,
    };
  }
  
  toggle = (index) => () => {
    this.setState({ [`block${index}`]: !this.state[`block${index}`] });
  }
  
  toggleExpand = (expand = false) => () => {
    this.setState({
      block1: expand,
      block2: expand,
      block3: expand,
    });
  }
  
  render() {
    const accordionList = [{ title: 'First Accordion' }, { title: 'Second Accordion' }, { title: 'Third Accordion' }];
    
      return (
          <div className={styles.container}>
            <h3>Factopedia</h3>
              <button type="button" className={styles.btn} onClick={this.toggleExpand(true)}>Expand All</button>
              <button type="button" className={styles.btn} onClick={this.toggleExpand()}>Collapse All</button>
              <div className={styles.accordion}>
            {
                accordionList.map((item, index) => (
                <Accordion title={item.title} onClick={this.toggle(index + 1)} expand={this.state[`block${index+1}`]} />
                ))
            }
            </div>
        </div>
      );
  }
}

